.Delete {
  display: flex;
  justify-content: center;
  align-items: center;
}

.DeleteArea {
  display: flex;
  justify-content: center;
}

.DeleteDropdowns {
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}

.DeleteDropdowns Label {
  font-size: 20px;
  font-weight: bolder;
  color: rgb(65, 63, 63) !important;
}
.DeleteDropdowns select {
  margin-bottom: 20px;
  height: 30px;
  width: 250px;
  border-color: rgb(182, 179, 179);
}
.DeleteDropdowns select :active,
.DeleteDropdowns select :focus {
  outline: none;
}
