.ant-dropdown .ant-dropdown-menu{
  display: flex !important;
  flex-direction: column !important;
}
.optionHead{
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 11px;
}

.option{
  margin-bottom: 6px !important;
  font-weight: 400;
  font-size: 14px;
}

.clearAll{
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #7c7e8c;
}
.clearAll img{
  width: 14px;
  margin-right: 10px;
}
.clearAll span{
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.05em;
}