.Edit {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Edit_fields {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.Edit_fields_name {
  font-size: 1rem;
  display: flex;
  width: 350px;
  justify-content: space-between;
}
.Edit_fields_name p {
  font-size: 18px;
  font-weight: bolder;
  color: rgb(68, 68, 68) !important;
}

.Edit_fields_input {
  margin: 0;
  padding-left: 10px;
  height: 30px;
  width: 250px;
  border: none;
  border-bottom: solid 0.5px grey;
  border-radius: 2px;
}
.Edit_fields_input:focus {
  outline: none;
}

.Sumbit_button {
  text-align: center;
}
.Sumbit_button button {
  outline: none;
  width: 200px;
  padding: 5px;
  background: rgb(22, 27, 34);
  color: white;
}

.Sumbit_button button:hover {
  opacity: 0.8;
}

.examples {
  margin-bottom: 2.4rem;
  color: grey;
}
