.ant-collapse-header{
  position: relative;
  display: flex;
  align-items: center;
}

.ant-collapse-header h6{
  margin-bottom: 0px;
  line-height: normal;
}

.ant-collapse-header .deleteCourseIcon{
  position: absolute;
  right: 30px;
  font-size: 12px !important;
  color: red;
  border: red 1.2px solid;
  padding: 4px 7px;
  cursor: pointer;
  background-color: rgb(255, 241, 241);
}

.ant-collapse-header .deleteCourseIcon:hover{
  background-color: rgb(255, 228, 228);
}

.ant-tabs-tabpane .deleteBranchIcon{
  background-color: red;
  font-size: 12px;
  padding: 7px;
  text-align: center;
  background-color: rgb(255, 241, 241);
  border: red 1.2px solid;
  cursor: pointer;
}

.ant-tabs-tabpane .deleteBranchIcon:hover{
  background-color: rgb(255, 228, 228);
}

.loading{
  font-family:'Monserrat';
  font-weight:300;
  font-size:16px;
  display:flex;
  flex-direction:column;
  width:100%;
  height:calc(100vh - 140px);
  justify-content:center;
  align-items:center;
}