

.Navbar nav {
  background: #161b22;
  margin-bottom: 2rem;
  position: sticky;
  top: 0;
}

.Navbar nav ul {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 70px;
  margin-left: 30px;
}

.Navbar nav ul a {
  margin-right: 2rem;
}

.Navbar nav ul li {
  color: white;
  cursor: pointer;
}

.Navbar nav ul li:hover {
  opacity: 0.8;
}
.logout {
  margin-left: auto;
  margin-right: 70px;
  background: rgb(231, 28, 28);
  padding: 8px;
  border-radius: 4px;
}
.logo{
  margin-right: 32px;
  width:40px;
  height:40px;
  /* object-fit: contain; */
}