.logo {
  margin-left: 5rem;
  white-space: nowrap;
  color: #546076;
  font-size: 24px;
  font-style: italic;
}

.logo_image {
  margin-right: 0.7rem;
}

.navbarHeading {
  margin-right: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.feature_Text {
  cursor: pointer;
  position: relative;
  margin-right: 2.5rem;
  color: #546076;
  font-weight: 600;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 0.04em;
  user-select: none;
}

.getMobile {
  cursor: pointer;
  user-select: none;
  background: #4385f5;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  transition: 0.6s;
}

.getMobile span {
  overflow: none;
  white-space: nowrap;
  color: white;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.003em;
}
.getMobile span::after {
  content: "Get Mobile app";
}
.feature_Text:after {
  content: "";
  color: rgba(0, 26, 90, 0.08);
  position: absolute;
  left: 0;
  display: inline-block;
  height: 1em;
  width: 100%;
  border-bottom: 1px solid;
  margin-top: 10px;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: scale(0, 1);
  transform: scale(0, 1);
}

.feature_Text:hover:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.feature_Text:active {
  color: rgb(0, 0, 0);
}

.getMobile:hover {
  background: #3d71cc;
}

