@media (max-width: 768px) {
  .feature_Text {
    display: none;
  }
  .getMobile {
    border-radius: 4px;
    padding: 8px 12px;
  }
  .getMobile span {
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */
  }
  .getMobile span::after {
    content: "Mobile app";
  }
  .navbar {
    box-shadow: inset 0px -1px 0px #e5e8ef;
  }
  .logo {
    margin-right: 5rem;
  }

  .navbarHeading {
    margin-right: 5rem;
  }

  .getMobile:hover {
    background: #4385f5;
  }
}

@media (max-width: 425px) {
  .navbar {
    height: 65px;
  }

  .logo {
    margin-left: 2rem;
  }

  .logo_image {
    margin-right: 0.4rem;
  }

  .navbarHeading {
    margin-right: 1rem;
  }
  .getMobile span {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.04em;
  }
}

@media (max-width: 530px) {
  .logo {
    margin-right: 2rem;
  }

  .navbarHeading {
    margin-right: 2rem;
  }
}

@media (max-width: 310px) {
  .logo {
    margin: 10px;
  }

  .navbarHeading {
    margin-right: 20px;
  }
}
