.AddPapers {
  margin: 0 auto;
  width: 400px;
  height: auto;
}

.AddPapers_Container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.AddPapers_Container label {
  font-size: 1rem;
  padding-top: 10px;
  white-space: nowrap;
}

.AddPapers_Container select {
  padding: 5px;
  height: 30px;
  width: 250px;
  border-color: rgb(182, 179, 179);
}

.videoUpload {
  margin-right: 70px;
}

.sumbit {
  text-align: center;
  margin-top: 2rem;
}
.sumbit button {
  outline: none;
  width: 200px;
  padding: 5px;
  background: rgb(22, 27, 34);
  color: white;
}
.sumbit button:hover {
  opacity: 0.8;
}



/* For modal */
.docsList{
  min-width: 65vw;
  position: relative;
}
.docsList h5{
  display: flex;
  align-items: center;
}
.docsList i{
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
}
.docsList i:hover{
  transform: scale(1.2);
}
.docsList .subHead{
  font-weight: 400;
}
.docsList .listDisplay{
  display: flex;
  flex-wrap: wrap;
  min-height: calc(65vh - 80px);
  max-height: calc(65vh - 80px);
  overflow-y: scroll;
  padding: 14px 0px;
}
.docsList .listDisplay .papers{
  border: #ccc 1.2px solid;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 12px;
  display: flex;
  align-items: center;
  margin-right: 20px;
}

