.container{
  width: 100%;
  height: 112px;
  border: 2px dashed black;
  border-radius: 8px;
}

.dropzone{
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}

.dropzone img{
  margin-bottom: 7px;
}

.dropzone h6{
  font-size: 12.9px;
  color: black;
}